// Scroll
::-webkit-scrollbar-track {
  border-radius: 12px;
  background-color: $color-secondary;
}

::-webkit-scrollbar {
  width: 6px;
  background-color: $color-secondary;
  border-radius: 3px;
}

::-webkit-scrollbar-thumb {
  border-radius: 12px;
  background-color: $scroll-bar;
}
